import React, { useState, useEffect } from "react";

const AstromonBaseCard = ({ onSelect }) => {
  const [astromons, setAstromons] = useState([]);
  const [filteredAstromons, setFilteredAstromons] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [activeElements, setActiveElements] = useState({
    fire: false,
    water: false,
    wood: false,
    light: false,
    dark: false,
  });
  const [selectedAstromons, setSelectedAstromons] = useState([]);

  useEffect(() => {
    const fetchAstromons = async () => {
      try {
        const response = await fetch("data/astromon-db-reduced.json");
        const data = await response.json();
        setAstromons(data);
        setFilteredAstromons(data);
      } catch (error) {
        console.error("Error loading astromon data:", error);
      }
    };

    fetchAstromons();
  }, []);

  // Toggle element filtering
  const toggleElement = (element) => {
    setActiveElements((prev) => ({
      ...prev,
      [element]: !prev[element],
    }));
  };

  // Update filtered astromons based on search and active elements
  useEffect(() => {
    const filtered = astromons.filter((astromon) => {
      const matchesSearch =
        astromon.family_slug
          .toLowerCase()
          .includes(searchQuery.toLowerCase()) ||
        astromon.name.toLowerCase().includes(searchQuery.toLowerCase());

      const activeElementKeys = Object.keys(activeElements).filter(
        (el) => activeElements[el]
      );
      const matchesElement =
        activeElementKeys.length === 0 ||
        activeElementKeys.includes(astromon.element);

      return (
        matchesSearch &&
        matchesElement &&
        (astromon.evolution === 3 || astromon.evolution === 4)
      );
    });

    setFilteredAstromons(filtered.slice(0, 20)); // Limit to first 20 results
  }, [searchQuery, activeElements, astromons]);

  const handleSelect = (astromon) => {
    if (
      !selectedAstromons.some((selected) => selected.slug === astromon.slug)
    ) {
      setSelectedAstromons((prev) => [...prev, astromon]);
      onSelect(astromon); // Pass the selected astromon back to the parent
    }
  };

  return (
    <div className="tool-card name-card">
      {/* Search box */}
      <input
        type="text"
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        placeholder="Search by family slug or name"
      />

      {/* Element toggle buttons */}
      <div>
        {["fire", "water", "wood", "light", "dark"].map((element) => (
          <button
            key={element}
            onClick={() => toggleElement(element)}
            style={{
              backgroundColor: activeElements[element] ? "lightblue" : "white",
            }}
          >
            {element}
          </button>
        ))}
      </div>

      {/* Filtered astromon results */}
      <div className="astromon-list">
        {filteredAstromons.map((astromon) => (
          <div
            key={astromon.uid}
            className="astromon-item"
            onClick={() => handleSelect(astromon)} // Pass the selected astromon back to the parent
            style={{
              cursor: "pointer",
              padding: "10px",
              border: "1px solid black",
              margin: "5px",
            }}
          >
            <img
              src={`icons/${astromon.icon_name}`} // Path to the icons folder
              alt={astromon.icon_name} // Alternative text for the image
              style={{ width: "50px", height: "50px" }} // Adjust size as needed
            />
            <span>{astromon.name}</span> {/* Display the astromon's name */}
          </div>
        ))}
      </div>

      {/* Display selected astromons */}
      <div className="selected-astromons">
        {selectedAstromons.map((astromon) => (
          <div key={astromon.slug} className="astromon-card">
            <img
              className="astromon-image"
              src={`/path-to-icons/${astromon.icon_name}.png`} // Replace with the correct path to your icons
              alt={astromon.name}
            />
            <ul className="astromon-stats">
              <li className="stat-item">HP: {astromon.hp}</li>
              <li className="stat-item">ATK: {astromon.atk}</li>
              <li className="stat-item">DEF: {astromon.def}</li>
              <li className="stat-item">REC: {astromon.rec}</li>
            </ul>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AstromonBaseCard;
